
import DeleteIcon from "@/components/Icons/DeleteIcon/DeleteIcon.vue";
import MenuIcon from "@/components/Icons/MenuIcon/MenuIcon.vue";
import { useWeatherStore } from "@/store/weather";
import { storeToRefs } from "pinia";

export default {
  components: { DeleteIcon, MenuIcon },
  setup() {
    const { startDrag, deleteCity, finishDrag, onDragOver } = useWeatherStore();
    const { getWeather, getOver, getDragFrom, getDragging, getStartLoc } =
      storeToRefs(useWeatherStore());
    return {
      getWeather,
      startDrag,
      finishDrag,
      onDragOver,
      deleteCity,
      getOver,
      getDragFrom,
      getDragging,
      getStartLoc,
    };
  },
};
